import React from "react";
import "./App.css";
import Arweave from "arweave";

const arweave = Arweave.init({
  host: "arweave.dev",
  port: 443,
  protocol: "https",
});

function App() {
  const [address, setAddress] = React.useState("");
  const [toAddress, setTo] = React.useState("");
  const [message, setMessage] = React.useState("");
  const [amount, setAmount] = React.useState("0");
  const doTxn = async () => {
    let txn = await arweave.createTransaction(
      {
        data: Buffer.from(message, "utf8"),
        target: toAddress ?? undefined,
        quantity: arweave.ar.arToWinston(amount),
      },
      injectedWallet.publicKey
    );
    txn.addTag("Content-Type", "text/html");
    console.log(txn);
    window.injectedWallet.sign(txn);
  };

  const connectWallet = async () => {
    let addr = "";
    if (window.hasOwnProperty("injectedWallet")) {
      addr = await arweave.wallets.jwkToAddress(
        window.injectedWallet.publicKey
      );
      setAddress(addr);
    } else alert("No wallet found");
  };

  return (
    <div className="App">
      <button
        disabled={address}
        onClick={connectWallet}
        style={{ marginRight: "5px" }}
      >
        Connect Wallet
      </button>

      {address && <p>Injected Wallet Address: {address}</p>}
      <div style={{display: "flex", flexDirection: 'column', width:'25%', position:'absolute', left:'50%'}}>
        <input
          type="text"
          placeholder="Message"
          onChange={(evt) => setMessage(evt.target.value)}
        />
        <input
          type="text"
          placeholder="To address"
          onChange={(evt) => setTo(evt.target.value)}
        />
        <input
          type="text"
          placeholder="Amount to send"
          onChange={(evt) => setAmount(evt.target.value)}
        />
              <button disabled={!address} onClick={doTxn}>
        Create and send transaction
      </button>
      </div>
    </div>
  );
}

export default App;
